import React, { useState } from "react"
import PropTypes from "prop-types"
import ReactCanvasTimeSeries, {
  canvas,
  layout,
  zoomable,
  draggable,
} from "react-canvas-time-series"
import { get } from "../../../util"
import { timeZone } from "../../../util/date"
import { roundInterval } from "../../../util/math"
import disableScroll from "../../../components/disableScroll"
import Labels from "../../../components/Labels"
import styles from "./BarGraph.module.css"

const Graph = zoomable(draggable(ReactCanvasTimeSeries.Graph))
const { getLayout } = layout

const MIN_BAR_WIDTH = 5
const BAR_PADDING = 5

const HEIGHT = 200

const Y_MIN = 0

function getNumXTicks(unit) {
  switch (unit) {
    case "MONTH":
      return 12
    case "DAY":
      return 6
    case "HOUR":
      return 6
    default:
      console.error(`Invalid unit ${unit}`)
  }
}

function renderXLabel(unit, x) {
  if (x == undefined || isNaN(x)) {
    return
  }

  const now = new Date()

  switch (unit) {
    case "MONTH":
      return timeZone(now.toISOString())
        .startOf("month")
        .add(Math.round(x), "month")
        .format("YYYY-MM")
    case "DAY":
      return timeZone(now.toISOString())
        .startOf("day")
        .add(Math.round(x), "day")
        .format("YYYY-MM-DD")
    case "HOUR":
      return timeZone(now.toISOString())
        .startOf("hour")
        .add(Math.round(x), "hour")
        .format("YYYY-MM-DD HH:mm")
    default:
      return
  }
}

function renderYLabel(y) {
  if (!y) {
    return "0 kWh"
  }

  return `${Math.round(y).toLocaleString()} kWh`
}

export function BarGraph(props) {
  const {
    width,
    data,
    yMaxs,
    xs,
    loading,
    devices,
    checked,
    colors,
    xMin,
    xMax,
    unit,
    onChangeXRange,
  } = props

  const [mouse, setMouse] = useState({
    x: undefined,
    y: undefined,
  })

  const [nearest, setNearest] = useState({
    timestamp: undefined,
    diffs: {},
  })

  const { graph } = getLayout({
    padding: 10,
    width,
    height: HEIGHT,
    xAxisAt: "bottom",
    xAxisHeight: 20,
    yAxisAt: "right",
    yAxisWidth: 70,
  })

  const checkedDevices = devices.filter(device => checked[device.id])
  const yMax = Math.max(...checkedDevices.map(device => yMaxs[device.id]), 10)

  function onMouseMove(e, mouse, graph, xRange) {
    if (canvas.math.isInside(graph, mouse)) {
      props.disableScroll()

      setMouse(mouse)
      const x = canvas.math.getX(graph.width, graph.left, xMax, xMin, mouse.x)
      const i = canvas.math.findNearestIndex(xs, x)

      const timestamp = get(data, [get(devices, [0, "id"]), i, "timestamp"])

      if (timestamp) {
        setNearest({
          timestamp,
          diffs: checkedDevices.reduce((diffs, device) => {
            diffs[device.id] = get(data, [device.id, i, "diff"], 0)
            return diffs
          }, {}),
        })
      }
    } else {
      props.enableScroll()

      setMouse({
        x: undefined,
        y: undefined,
      })

      setNearest({
        timestamp: undefined,
        diffs: {},
      })
    }

    onChangeXRange(xRange)
  }

  function onMouseOut() {
    props.enableScroll()

    setMouse({
      x: undefined,
      y: undefined,
    })

    setNearest({
      timestamp: undefined,
      diffs: {},
    })
  }

  function onWheel(e, mouse, graph, xRange) {
    onChangeXRange(xRange)
  }

  const barWidth = Math.max(
    MIN_BAR_WIDTH,
    Math.floor(graph.width / (xMax - xMin)) - 2 * BAR_PADDING
  )

  // TODO xTickInterval based on graph.width
  const xTickInterval = Math.max(
    1,
    Math.floor((xMax - xMin) / getNumXTicks(unit))
  )
  const yTickInterval = roundInterval(yMax, Y_MIN, 4)

  const graphs = checkedDevices.map(device => ({
    type: "bars",
    barWidth,
    getBarColor: () => colors[device.id],
    data: data[device.id] || [],
  }))

  return (
    <div className={styles.component}>
      <Graph
        width={width}
        height={HEIGHT}
        backgroundColor={loading ? "whitesmoke" : "beige"}
        mouse={mouse}
        xMin={xMin}
        xMax={xMax}
        yMin={Y_MIN}
        yMax={yMax}
        xAxisAt="bottom"
        xAxisHeight={20}
        xAxisLineColor="lightgrey"
        xTickInterval={xTickInterval}
        xTickLength={5}
        renderXTick={x => renderXLabel(unit, x)}
        showXLine={false}
        yAxisAt="right"
        yAxisWidth={70}
        yAxisLineColor="lightgrey"
        yTickInterval={yTickInterval}
        yTickLength={5}
        renderYTick={y => `${Math.round(y).toLocaleString()} kWh`}
        graphs={graphs}
        xLabels={[
          {
            x: canvas.math.getX(graph.width, graph.left, xMax, xMin, mouse.x),
            width: 100,
            height: 20,
            render: x => renderXLabel(unit, x),
            color: "white",
            backgroundColor: "black",
            lineColor: "black",
          },
        ]}
        yLabels={[
          {
            y: yMax,
            width: 70,
            height: 20,
            render: y => renderYLabel(y),
            color: "black",
            backgroundColor: "orange",
            lineColor: "orange",
          },
          {
            y: canvas.math.getY(graph.height, graph.top, yMax, Y_MIN, mouse.y),
            width: 70,
            height: 20,
            render: y => renderYLabel(y),
            color: "white",
            backgroundColor: "black",
            lineColor: "black",
          },
        ]}
        onMouseMove={onMouseMove}
        onMouseOut={onMouseOut}
        onWheel={onWheel}
      />
      <div className={styles.labels}>
        <Labels
          timestamp={nearest.timestamp}
          labels={checkedDevices.map(device => ({
            id: device.id,
            color: colors[device.id],
            value: `${get(nearest.diffs, [device.id], 0)} kWh`,
          }))}
        />
      </div>
    </div>
  )
}

BarGraph.propTypes = {
  width: PropTypes.number.isRequired,
  data: PropTypes.object.isRequired,
  yMaxs: PropTypes.object.isRequired,
  xs: PropTypes.arrayOf(PropTypes.number).isRequired,
  loading: PropTypes.bool.isRequired,
  unit: PropTypes.oneOf(["MONTH", "DAY", "HOUR"]),
  onChangeXRange: PropTypes.func.isRequired,
  disableScroll: PropTypes.func.isRequired,
  enableScroll: PropTypes.func.isRequired,
}

export default disableScroll(BarGraph)
