import React from "react"
import PropTypes from "prop-types"
import { Graph } from "react-canvas-time-series"
import { get } from "../../util"
import { timeZone } from "../../util/date"
import { roundInterval } from "../../util/math"
import * as api from "../../api"
import withFetch from "../../components/withFetch"
import Loading from "../../components/Loading"
import GraphPlaceholder from "./GraphPlaceholder"
import styles from "./FiscalEnergyGraph.module.css"

const MIN_Y_MAX = 1000
const X_MIN = 0.5
const X_MAX = 12.5
const Y_MIN = 0

export function FiscalEnergyGraph(props) {
  const { width, height, getEnergy } = props

  const { fetching, fetchedAt, fetch, error } = getEnergy

  const simulation = get(getEnergy, ["response", "simulation"], [])
  const data = get(getEnergy, ["response", "data"], [])
  const yMax = Math.max(MIN_Y_MAX, get(getEnergy, ["response", "yMax"], 0))
  const energyLastMonth = get(data, [data.length - 2, "diff"], 0)
  const energyCurrentMonth = get(data, [data.length - 1, "diff"], 0)

  const yTickInterval = roundInterval(yMax, Y_MIN, 4)

  return (
    <Loading
      loading={fetching || !fetchedAt}
      error={error}
      onClickRetry={fetch}
      renderLoading={() => (
        <GraphPlaceholder
          width={width}
          height={height}
          backgroundColor="whitesmoke"
        />
      )}
    >
      <div>
        <div className={styles.stats}>
          <div className={styles.month}>
            Last month{" "}
            <span className={styles.number}>
              {energyLastMonth.toLocaleString()} kWh
            </span>
          </div>
          <div className={styles.month}>
            This month{" "}
            <span className={styles.number}>
              {energyCurrentMonth.toLocaleString()} kWh
            </span>
          </div>
        </div>
        <Graph
          width={width}
          height={height}
          backgroundColor="beige"
          animate={false}
          xMin={X_MIN}
          xMax={X_MAX}
          yMin={Y_MIN}
          yMax={yMax}
          xAxisAt="bottom"
          xAxisHeight={20}
          xAxisLineColor="lightgrey"
          xTickInterval={1}
          xTickLength={5}
          showXLine={false}
          yAxisAt="right"
          yAxisWidth={70}
          yAxisLineColor="lightgrey"
          yTickInterval={yTickInterval}
          renderYTick={y => `${Math.round(y).toLocaleString()} kWh`}
          yLabels={[
            {
              y: yMax,
              width: 70,
              height: 20,
              render: y => `${y.toLocaleString()} kWh`,
              color: "white",
              backgroundColor: "blue",
              drawLine: true,
              lineColor: "blue",
            },
          ]}
          graphs={[
            {
              type: "bars",
              barWidth: 16,
              getBarColor: () => "orange",
              data,
            },
            {
              type: "line",
              data: simulation,
              lineColor: "red",
              step: 1,
            },
          ]}
        />
      </div>
    </Loading>
  )
}

FiscalEnergyGraph.propTypes = {
  width: PropTypes.number.isRequired,
  height: PropTypes.number.isRequired,
  getEnergy: PropTypes.shape({
    fetching: PropTypes.bool.isRequired,
    error: PropTypes.string.isRequired,
    fetch: PropTypes.func.isRequired,
    fetchedAt: PropTypes.string.isRequired,
    response: PropTypes.shape({
      simulation: PropTypes.arrayOf(
        PropTypes.shape({
          x: PropTypes.number.isRequired,
          y: PropTypes.number.isRequired,
        })
      ).isRequired,
      data: PropTypes.arrayOf(
        PropTypes.shape({
          x: PropTypes.number.isRequired,
          y: PropTypes.number.isRequired,
        })
      ).isRequired,
      yMax: PropTypes.number.isRequired,
    }),
  }),
}

async function getEnergy(params, props) {
  const { plantId } = params

  const now = new Date()

  const year = now.getFullYear()

  const simulation = (await api.getSimulationData({
    plantId,
    yearGte: year,
    yearLte: year,
  })).map(({ year, month, energy }) => ({
    x: month + 1,
    y: energy,
  }))

  // Find the first cluster controller
  const device = props.plant.devices.find(
    device => device.model == "Cluster Controller"
  )

  const res = await api.getEnergy({
    plantId,
    deviceId: device.id,
    unit: "MONTH",
    timestamp: {
      // last year December
      gte: timeZone(now.toISOString())
        .startOf("year")
        .subtract(1, "month")
        .toISOString(),
      // end of current month
      lte: timeZone(now.toISOString())
        .endOf("month")
        .toISOString(),
    },
  })

  const data = res.map((energy, i) => ({
    ...energy,
    x: i + 1,
    y: energy.diff,
  }))

  const yMax = Math.max(...data.map(d => d.y), ...simulation.map(d => d.y))

  return {
    data,
    yMax,
    simulation,
  }
}

export default withFetch(getEnergy, {
  name: "getEnergy",
  getFetchId: (name, params) => `${name}-${params.plantId}`,
  getParams: props => ({ plantId: props.plant.id }),
  shouldFetch: (props, response) => !response,
})(FiscalEnergyGraph)
