import React, { useState } from "react"
import PropTypes from "prop-types"
import { Button } from "semantic-ui-react"
import DatePicker from "react-date-picker"
import { canvas } from "react-canvas-time-series"
import { connect } from "react-redux"
import { get, compose } from "../../../util"
import * as api from "../../../api"
import withParams from "../../../components/withParams"
import withFetch from "../../../components/withFetch"
import disableScroll from "../../../components/disableScroll"
import useToggle from "../../../components/useToggle"
import DeviceFilter from "../../../components/DeviceFilter"
import Labels from "../../../components/Labels"
import LineGraph from "./LineGraph"
import styles from "./index.module.css"

const X_MIN = Math.round(new Date().getTime() / 1000 - 5 * 24 * 3600)
const X_MAX = Math.round(new Date().getTime() / 1000)

// const NOW = Math.round(new Date().getTime() / 1000)
// const X_MIN = NOW - 9 * 24 * 3600
// const X_MAX = NOW + 24 * 3600

export function InverterGraphs(props) {
  const {
    plantId,
    devices,
    graphWidth,
    colors,
    getLogs,
    params,
    setParams,
  } = props

  const { fetching } = getLogs

  const data = get(getLogs, ["response"], {})
  const logs = get(data, ["logs"], {})
  const unixTimes = get(data, ["unixTimes"], {})

  const maxPower = get(data, ["maxPower"], 0)
  const maxCurrent = get(data, ["maxCurrent"], 0)
  const maxVoltage = get(data, ["maxVoltage"], 0)
  const maxInsulationResistance = get(data, ["maxInsulationResistance"], 0)
  const maxResidualCurrent = get(data, ["maxResidualCurrent"], 0)

  const [checked, toggle] = useToggle(devices.map(({ id }) => id))

  const [mouseX, setMouseX] = useState()

  const [nearest, setNearest] = useState({
    timestamp: undefined,
    power: {},
    current: {},
    voltage: {},
    insulationResistance: {},
    residualCurrent: {},
  })

  const [filterDates, setFilterDates] = useState({
    gte: new Date(X_MIN * 1000),
    lte: new Date(X_MAX * 1000),
  })

  const checkedDevices = devices.filter(device => checked[device.id])

  function setFilterDate(name, date) {
    setFilterDates({
      ...filterDates,
      [name]: date,
    })
  }

  function onClickFilter() {
    setParams({
      ...params,
      xMin: Math.round(filterDates.gte.getTime() / 1000),
      xMax: Math.round(filterDates.lte.getTime() / 1000),
    })
  }

  function onMouseMove(mouse, graph, xRange) {
    if (canvas.math.isInside(graph, mouse)) {
      props.disableScroll()

      setMouseX(mouse.x)

      const { xMin, xMax } = xRange

      const x = canvas.math.getX(graph.width, graph.left, xMax, xMin, mouse.x)
      const i = canvas.math.findNearestIndex(unixTimes, x)

      const unixTime = unixTimes[i]

      if (unixTime) {
        const nearest = {
          timestamp: new Date(unixTime * 1000).toISOString(),
          power: {},
          current: {},
          voltage: {},
          insulationResistance: {},
          residualCurrent: {},
        }

        for (const device of checkedDevices) {
          nearest.power[device.id] = get(logs, [device.id, "power", i, "y"], 0)
          nearest.current[device.id] = get(
            logs,
            [device.id, "current", i, "y"],
            0
          )
          nearest.voltage[device.id] = get(
            logs,
            [device.id, "voltage", i, "y"],
            0
          )
          nearest.insulationResistance[device.id] = get(
            logs,
            [device.id, "insulationResistance", i, "y"],
            0
          )
          nearest.residualCurrent[device.id] = get(
            logs,
            [device.id, "residualCurrent", i, "y"],
            0
          )
        }

        setNearest(nearest)
      }
    } else {
      props.enableScroll()

      setMouseX()

      setNearest({
        timestamp: undefined,
        power: {},
        current: {},
        voltage: {},
        insulationResistance: {},
        residualCurrent: {},
      })
    }

    setParams({
      ...params,
      ...xRange,
    })
  }

  function onMouseOut() {
    props.enableScroll()

    setMouseX()

    setNearest({
      timestamp: undefined,
      power: {},
      current: {},
      voltage: {},
      insulationResistance: {},
      residualCurrent: {},
    })
  }

  function onWheel(xRange) {
    setParams({
      ...params,
      ...xRange,
    })
  }

  const { xMin, xMax } = params

  return (
    <div>
      <div className={styles.filters}>
        <DeviceFilter
          devices={devices}
          checked={checked}
          colors={colors}
          onToggle={toggle}
        />
        <div className={styles.dateFilters}>
          <DatePicker
            format="yyyy-MM-dd"
            value={filterDates.gte}
            disabled={fetching}
            onChange={date => setFilterDate("gte", date)}
          />
          <div> - </div>
          <DatePicker
            format="yyyy-MM-dd"
            value={filterDates.lte}
            disabled={fetching}
            onChange={date => setFilterDate("lte", date)}
          />
        </div>
        <Button loading={fetching} onClick={onClickFilter}>
          Filter
        </Button>
      </div>
      <h5 className={styles.graphHeader}>Power (watts)</h5>
      <div className={styles.row}>
        <LineGraph
          loading={fetching}
          lines={checkedDevices.map(device => ({
            data: get(logs, [device.id, "power"], []),
            color: colors[device.id],
          }))}
          width={graphWidth}
          unit="watts"
          yMax={maxPower}
          yMin={0}
          xMin={xMin}
          xMax={xMax}
          mouseX={mouseX}
          onMouseMove={onMouseMove}
          onMouseOut={onMouseOut}
          onWheel={onWheel}
        />
        <div className={styles.labels}>
          <Labels
            timestamp={nearest.timestamp}
            labels={checkedDevices.map(device => ({
              id: device.id,
              color: colors[device.id],
              value: `${get(nearest.power, [device.id], 0)} watts`,
            }))}
          />
        </div>
      </div>

      <h5 className={styles.graphHeader}>Current (amp)</h5>
      <div className={styles.row}>
        <LineGraph
          loading={fetching}
          plantId={plantId}
          lines={checkedDevices.map(device => ({
            data: get(logs, [device.id, "current"], []),
            color: colors[device.id],
          }))}
          width={graphWidth}
          unit="amp"
          yMax={maxCurrent}
          yMin={0}
          xMin={xMin}
          xMax={xMax}
          mouseX={mouseX}
          onMouseMove={onMouseMove}
          onMouseOut={onMouseOut}
          onWheel={onWheel}
        />
        <div className={styles.labels}>
          <Labels
            timestamp={nearest.timestamp}
            labels={checkedDevices.map(device => ({
              id: device.id,
              color: colors[device.id],
              value: `${get(nearest.current, [device.id], 0)} amp`,
            }))}
          />
        </div>
      </div>

      <h5 className={styles.graphHeader}>Voltage (v)</h5>
      <div className={styles.row}>
        <LineGraph
          loading={fetching}
          lines={checkedDevices.map(device => ({
            data: get(logs, [device.id, "voltage"], []),
            color: colors[device.id],
          }))}
          width={graphWidth}
          unit="v"
          yMax={maxVoltage}
          yMin={0}
          xMin={xMin}
          xMax={xMax}
          mouseX={mouseX}
          onMouseMove={onMouseMove}
          onMouseOut={onMouseOut}
          onWheel={onWheel}
        />
        <div className={styles.labels}>
          <Labels
            timestamp={nearest.timestamp}
            labels={checkedDevices.map(device => ({
              id: device.id,
              color: colors[device.id],
              value: `${get(nearest.voltage, [device.id], 0)} v`,
            }))}
          />
        </div>
      </div>

      <h5 className={styles.graphHeader}>Insulation Resistance (kOhm)</h5>
      <div className={styles.row}>
        <LineGraph
          loading={fetching}
          lines={checkedDevices.map(device => ({
            data: get(logs, [device.id, "insulationResistance"], []),
            color: colors[device.id],
          }))}
          width={graphWidth}
          unit="kOhm"
          yMax={maxInsulationResistance}
          yMin={0}
          xMin={xMin}
          xMax={xMax}
          mouseX={mouseX}
          onMouseMove={onMouseMove}
          onMouseOut={onMouseOut}
          onWheel={onWheel}
        />
        <div className={styles.labels}>
          <Labels
            timestamp={nearest.timestamp}
            labels={checkedDevices.map(device => ({
              id: device.id,
              color: colors[device.id],
              value: `${get(
                nearest.insulationResistance,
                [device.id],
                0
              )} kOhm`,
            }))}
          />
        </div>
      </div>

      <h5 className={styles.graphHeader}>Residual Current (mA)</h5>
      <div className={styles.row}>
        <LineGraph
          loading={fetching}
          lines={checkedDevices.map(device => ({
            data: get(logs, [device.id, "residualCurrent"], []),
            color: colors[device.id],
          }))}
          width={graphWidth}
          unit="mA"
          yMax={maxResidualCurrent}
          yMin={0}
          xMin={xMin}
          xMax={xMax}
          mouseX={mouseX}
          onMouseMove={onMouseMove}
          onMouseOut={onMouseOut}
          onWheel={onWheel}
        />
        <div className={styles.labels}>
          <Labels
            timestamp={nearest.timestamp}
            labels={checkedDevices.map(device => ({
              id: device.id,
              color: colors[device.id],
              value: `${get(nearest.residualCurrent, [device.id], 0)} mA`,
            }))}
          />
        </div>
      </div>
    </div>
  )
}

InverterGraphs.propTypes = {
  graphWidth: PropTypes.number.isRequired,
  plantId: PropTypes.string.isRequired,
  devices: PropTypes.arrayOf(
    PropTypes.shape({
      model: PropTypes.string.isRequired,
      id: PropTypes.string.isRequired,
    })
  ).isRequired,
  disableScroll: PropTypes.func.isRequired,
  enableScroll: PropTypes.func.isRequired,
}

async function getLogs(params, props) {
  const { plantId, deviceIds, xMin, xMax } = params

  const gte = new Date(xMin * 1000).toISOString()
  const lte = new Date(xMax * 1000).toISOString()
  const step = 5

  const res = await Promise.all(
    deviceIds.map(async deviceId => {
      const data = await api.getLogs({
        plantId,
        deviceId,
        gte,
        lte,
        step,
      })

      return {
        deviceId,
        data,
      }
    })
  )

  const unixTimes = get(res, [0, "data"], []).map(({ unixTime }) => unixTime)

  let maxPower = 0
  let maxCurrent = 0
  let maxVoltage = 0
  let maxInsulationResistance = 0
  let maxResidualCurrent = 0

  const logs = {}

  for (const { deviceId, data } of res) {
    logs[deviceId] = {
      power: [],
      current: [],
      voltage: [],
      insulationResistance: [],
      residualCurrent: [],
    }

    for (const d of data) {
      logs[deviceId].power.push({
        x: d.unixTime,
        y: d.power,
      })

      logs[deviceId].current.push({
        x: d.unixTime,
        y: d.current,
      })

      logs[deviceId].voltage.push({
        x: d.unixTime,
        y: d.voltage,
      })

      logs[deviceId].insulationResistance.push({
        x: d.unixTime,
        y: d.insulationResistance,
      })

      logs[deviceId].residualCurrent.push({
        x: d.unixTime,
        y: d.residualCurrent,
      })

      maxPower = Math.max(d.power, maxPower)
      maxCurrent = Math.max(d.current, maxCurrent)
      maxVoltage = Math.max(d.voltage, maxVoltage)
      maxInsulationResistance = Math.max(
        d.insulationResistance,
        maxInsulationResistance
      )
      maxResidualCurrent = Math.max(d.residualCurrent, maxResidualCurrent)
    }
  }

  return {
    maxPower,
    maxCurrent,
    maxVoltage,
    maxInsulationResistance,
    maxResidualCurrent,
    logs,
    unixTimes,
  }
}

export default compose(
  connect(state => ({ divSize: state.divSize })),
  disableScroll,
  withParams({
    xMin: X_MIN,
    xMax: X_MAX,
  }),
  withFetch(getLogs, {
    name: "getLogs",
    getFetchId: (name, params) => `${name}-${params.plantId}`,
    debounce: 500,
    getParams: props => ({
      plantId: props.plantId,
      deviceIds: props.devices.map(({ id }) => id),
      xMin: props.params.xMin,
      xMax: props.params.xMax,
    }),
    saveCache: (props, response) => {},
    shouldFetch: (props, repsonse, params) => true,
  })
)(InverterGraphs)
