import React from "react"
import PropTypes from "prop-types"
import LinkButton from "../../components/LinkButton"
import styles from "./PlantHeader.module.css"

export function PlantHeader(props) {
  const { plant, onClick } = props

  return (
    <div className={styles.component}>
      <LinkButton onClick={onClick}>Plant {plant.id}</LinkButton>
      <div className={styles.devices}>{plant.devices.length} devices</div>
    </div>
  )
}

PlantHeader.defaultProps = {
  plant: PropTypes.shape({
    id: PropTypes.string.isRequired,
    devices: PropTypes.array.isRequired,
  }).isRequired,
  onClick: PropTypes.func.isRequired,
}

export default PlantHeader
