import React from "react"
import PropTypes from "prop-types"
import { connect } from "react-redux"
import { get, compose, chunk } from "../../util"
import * as api from "../../api"
import withFetch from "../../components/withFetch"
import Loading from "../../components/Loading"
import styles from "./index.module.css"
import PlantHeader from "./PlantHeader"
import FiscalEnergyGraph from "./FiscalEnergyGraph"

const PADDING = 10
const MIN_GRAPH_WIDTH = 340
const GRAPH_HEIGHT = 210

export function getNumGraphsPerRow(width) {
  if (width < MIN_GRAPH_WIDTH * 2 + 2 * PADDING) {
    return 1
  }
  if (width < MIN_GRAPH_WIDTH * 3 + 2 * PADDING) {
    return 2
  }
  if (width < MIN_GRAPH_WIDTH * 5 + 2 * PADDING) {
    return 3
  }

  return 5
}

function Graph(props) {
  const { width, plant, onClick } = props
  const padding = 10

  return (
    <div>
      <div className={styles.plantHeader}>
        <PlantHeader plant={plant} onClick={onClick} />
      </div>
      <div style={{ padding, paddingTop: 0 }}>
        <FiscalEnergyGraph
          width={width - 2 * padding}
          height={GRAPH_HEIGHT}
          plant={plant}
        />
      </div>
    </div>
  )
}

export function HomePage(props) {
  const { divSize, getPowerPlants } = props

  const numGraphsPerRow = getNumGraphsPerRow(divSize.width)
  const graphWidth = Math.max(
    Math.floor((divSize.width - 2 * PADDING) / numGraphsPerRow),
    MIN_GRAPH_WIDTH
  )

  const count = get(getPowerPlants, ["response", "count"], 0)
  const data = get(getPowerPlants, ["response", "data"], [])

  return (
    <Loading
      loading={getPowerPlants.fetching || !getPowerPlants.fetchedAt}
      error={getPowerPlants.error}
      onClickRetry={getPowerPlants.fetch}
    >
      <div className={styles.count}>{count} power plants</div>
      <div className={styles.col}>
        {chunk(data, numGraphsPerRow).map((plants, i) => (
          <div key={i} className={styles.row}>
            {plants.map(plant => (
              <Graph
                key={plant.id}
                width={graphWidth}
                plant={plant}
                onClick={() => props.history.push(`/plants/${plant.id}`)}
              />
            ))}
          </div>
        ))}
      </div>
    </Loading>
  )
}

HomePage.propTypes = {
  divSize: PropTypes.shape({
    height: PropTypes.number.isRequired,
    width: PropTypes.number.isRequired,
  }).isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
  getPowerPlants: PropTypes.shape({
    error: PropTypes.string.isRequired,
    fetching: PropTypes.bool.isRequired,
    fetch: PropTypes.func.isRequired,
    fetchedAt: PropTypes.string.isRequired,
    response: PropTypes.shape({
      data: PropTypes.array.isRequired,
      count: PropTypes.number.isRequired,
    }),
  }).isRequired,
}

export default compose(
  connect(state => ({ divSize: state.divSize })),
  withFetch(api.getPowerPlants, {
    name: "getPowerPlants",
    shouldFetch: (props, response) => !response,
  })
)(HomePage)
