import React, { useState } from "react"
import PropTypes from "prop-types"
import ReactCanvasTimeSeries, {
  canvas,
  zoomable,
  draggable,
  layout,
} from "react-canvas-time-series"
import { roundInterval } from "../../../util/math"
import { timeZone } from "../../../util/date"

const Graph = zoomable(draggable(ReactCanvasTimeSeries.Graph))
const { getLayout } = layout

const HEIGHT = 150

function renderXLabel(x) {
  if (x == undefined || isNaN(x)) {
    return
  }

  return timeZone(x * 1000).format("MM DD HH:mm")
}

function renderYLabel(y, unit) {
  if (!y) {
    return `${y} ${unit}`
  }

  return `${Math.round(y).toLocaleString()} ${unit}`
}

export function LineGraph(props) {
  const { loading, width, lines, yMin, yMax, xMin, xMax, mouseX, unit } = props

  const [mouseY, setMouseY] = useState()

  function onMouseMove(e, mouse, graph, xRange) {
    if (canvas.math.isInside(graph, mouse)) {
      setMouseY(mouse.y)
    } else {
      setMouseY()
    }

    props.onMouseMove(mouse, graph, xRange)
  }

  function onMouseOut() {
    setMouseY()
    props.onMouseOut()
  }

  function onWheel(e, mouse, graph, xRange) {
    props.onWheel(xRange)
  }

  const xTickInterval = 24 * 3600
  const yTickInterval = roundInterval(yMax, yMin, 4)

  const graphs = lines.map((line) => ({
    type: "line",
    data: line.data,
    lineColor: line.color,
    step: 1,
  }))

  const { graph } = getLayout({
    padding: 10,
    width,
    height: HEIGHT,
    xAxisAt: "bottom",
    xAxisHeight: 20,
    yAxisAt: "right",
    yAxisWidth: 70,
  })

  const mouse = {
    x: mouseX,
    y: mouseY,
  }

  return (
    <Graph
      width={width}
      height={HEIGHT}
      backgroundColor={loading ? "whitesmoke" : "beige"}
      mouse={mouse}
      xMin={xMin}
      xMax={xMax}
      yMin={yMin}
      yMax={yMax}
      xAxisAt="bottom"
      xAxisHeight={20}
      xAxisLineColor="lightgrey"
      xTickInterval={xTickInterval}
      xTickLength={5}
      renderXTick={renderXLabel}
      showXLine={true}
      yAxisAt="right"
      yAxisWidth={70}
      yAxisLineColor="lightgrey"
      yTickInterval={yTickInterval}
      yTickLength={5}
      renderYTick={(y) => renderYLabel(y, unit)}
      graphs={graphs}
      xLabels={[
        {
          x: canvas.math.getX(graph.width, graph.left, xMax, xMin, mouseX),
          width: 100,
          height: 20,
          render: renderXLabel,
          color: "white",
          backgroundColor: "black",
          drawLine: true,
          lineColor: "black",
        },
      ]}
      yLabels={[
        {
          y: canvas.math.getY(graph.height, graph.top, yMax, yMin, mouseY),
          width: 70,
          height: 20,
          render: (y) => renderYLabel(y, unit),
          color: "white",
          backgroundColor: "black",
          drawLine: true,
          lineColor: "black",
        },
      ]}
      onMouseMove={onMouseMove}
      onMouseOut={onMouseOut}
      onWheel={onWheel}
    />
  )
}

LineGraph.propTypes = {
  loading: PropTypes.bool.isRequired,
  width: PropTypes.number.isRequired,
  xMin: PropTypes.number.isRequired,
  xMax: PropTypes.number.isRequired,
  mouseX: PropTypes.number,
  lines: PropTypes.arrayOf(
    PropTypes.shape({
      color: PropTypes.string.isRequired,
      data: PropTypes.arrayOf(
        PropTypes.shape({
          x: PropTypes.number.isRequired,
          y: PropTypes.number.isRequired,
        })
      ).isRequired,
    })
  ).isRequired,
  unit: PropTypes.oneOf(["watts", "amp", "v", "kOhm", "mA"]).isRequired,
}

export default LineGraph
