import React from "react"
import PropTypes from "prop-types"
import { connect } from "react-redux"
import { get, compose } from "../../util"
import * as api from "../../api"
import COLORS from "../../lib/colors"
import { bound } from "../../util/math"
import withFetch from "../../components/withFetch"
import Loading from "../../components/Loading"
import styles from "./index.module.css"
import EnergyGraph from "./EnergyGraph"
import InverterGraphs from "./InverterGraphs"

const GRAPH_PADDING = 10
const MIN_GRAPH_WIDTH = 300
const MAX_GRAPH_WIDTH = 1200

export function PowerPlantDetailPage(props) {
  const { divSize, getPowerPlant } = props

  const devices = get(getPowerPlant, ["response", "devices"], [])

  const colors = devices.reduce((colors, device, i) => {
    colors[device.id] = COLORS[i]
    return colors
  }, {})

  const graphWidth = bound(
    divSize.width - 2 * GRAPH_PADDING,
    MIN_GRAPH_WIDTH,
    MAX_GRAPH_WIDTH
  )

  return (
    <div>
      <div className={styles.header}>
        <div className={styles.headerText}>
          Plant {props.match.params.plantId}
        </div>
        <div className={styles.subHeader}>{devices.length} devices</div>
      </div>
      <Loading
        loading={getPowerPlant.fetching || !getPowerPlant.fetchedAt}
        error={getPowerPlant.error}
        onClickRetry={getPowerPlant.fetch}
      >
        {devices.length > 0 && (
          <>
            <EnergyGraph
              plantId={props.match.params.plantId}
              devices={devices}
              graphWidth={graphWidth}
              colors={colors}
            />
            <InverterGraphs
              plantId={props.match.params.plantId}
              devices={devices.filter(
                (device) => device.model != "Cluster Controller"
              )}
              graphWidth={graphWidth}
              colors={colors}
            />
          </>
        )}
      </Loading>
    </div>
  )
}

PowerPlantDetailPage.propTypes = {
  match: PropTypes.shape({
    params: PropTypes.shape({
      plantId: PropTypes.string.isRequired,
    }).isRequired,
  }).isRequired,
  divSize: PropTypes.shape({
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired,
  }).isRequired,
  getPowerPlant: PropTypes.shape({
    response: PropTypes.shape({
      devices: PropTypes.array.isRequired,
    }),
    error: PropTypes.string.isRequired,
    fetching: PropTypes.bool.isRequired,
    fetch: PropTypes.func.isRequired,
    fetchedAt: PropTypes.string.isRequired,
  }).isRequired,
}

export default compose(
  connect((state) => ({ divSize: state.divSize })),
  withFetch((params) => api.getPowerPlant(params), {
    name: "getPowerPlant",
    getFetchId: (name, params) => `${name}-${params.id}`,
    getParams: (props) => ({ id: props.match.params.plantId }),
    shouldFetch: (props, response) => !response,
  })
)(PowerPlantDetailPage)
