const TIME_ZONE_OFFSET = parseInt(process.env.REACT_APP_TIME_ZONE_OFFSET)

async function sendQuery(url, query, variables) {
  const res = await fetch(url, {
    method: "POST",
    body: JSON.stringify({
      query,
      variables,
    }),
    headers: {
      "Content-Type": "application/json",
    },
  })

  const text = await res.text()

  try {
    const res = JSON.parse(text)

    if (res.errors) {
      console.error("GraphQL error", res.errors)
      throw new Error(res.errors[0].message)
    }

    return res
  } catch (error) {
    throw error
  }
}

export async function getPowerPlants() {
  const query = `
    query {
      powerPlants {
        count
        data {
          id
          devices {
            id
            model
          }
        }
      }
    }
  `

  const res = await sendQuery(process.env.REACT_APP_API_URL, query)
  return res.data.powerPlants
}

export async function getPowerPlant(params) {
  const { id } = params

  const query = `
    query PowerPlant($id: String!) {
      powerPlant(id: $id) {
        id
        devices {
          id
          model
        }
      }
    }
  `

  const variables = { id }

  const res = await sendQuery(process.env.REACT_APP_API_URL, query, variables)
  return res.data.powerPlant
}

export async function getEnergy(params) {
  const query = `
    query Energy($plantId: String!, $deviceId: String!, $unit: Unit! $timestamp: TimeZoneRangeInput!) {
      energy(plantId: $plantId, deviceId: $deviceId, unit: $unit, timestamp: $timestamp) {
        timestamp
        energy
      }
    }
  `

  const {
    plantId,
    deviceId,
    unit,
    timestamp: { lte, gte },
  } = params

  const variables = {
    plantId,
    deviceId,
    unit,
    timestamp: {
      timeZoneOffset: TIME_ZONE_OFFSET,
      lte,
      gte,
    },
  }

  const res = await sendQuery(process.env.REACT_APP_API_URL, query, variables)
  const { energy } = res.data

  // replace 0 energy (possibly missing data) with min energy
  let min = Math.min(...energy.filter(d => d.energy > 0).map(d => d.energy))
  for (let i = 0; i < energy.length; i++) {
    min = Math.max(min, energy[i].energy)

    if (energy[i].energy == 0) {
      energy[i].energy = min
    }
  }

  const data = []

  for (let i = 1; i < energy.length; i++) {
    const diff = Math.floor(energy[i].energy) - Math.floor(energy[i - 1].energy)

    data.push({
      timestamp: energy[i].timestamp,
      energy: energy[i].energy,
      unixTime: Math.floor(new Date(energy[i].timestamp).getTime() / 1000),
      diff,
    })

    if (diff < 0) {
      console.warn(
        `Found negative energy diff for plant ${plantId} device ${deviceId}`,
        data
      )
    }
  }

  return data
}

export async function getSimulationData(params) {
  const { plantId, yearGte, yearLte } = params

  const query = `
      query Simulation($plantId: String!, $year: YearRangeInput!) {
        simulation(plantId: $plantId, year: $year) {
          year
          month
          energy
        }
      }
    `

  const variables = {
    plantId,
    year: { gte: yearGte, lte: yearLte },
  }

  const res = await sendQuery(process.env.REACT_APP_API_URL, query, variables)

  return res.data.simulation.map(({ year, month, energy }) => ({
    year,
    month,
    energy: parseFloat(energy),
  }))
}

export async function getLogs(params) {
  const query = `
    query Logs($plantId: String!, $deviceId: String!, $timestamp: TimestampRangeInput!) {
      logs(plantId: $plantId, deviceId: $deviceId, timestamp: $timestamp) {
        timestamp
        energy
        power
        current
        voltage
        insulationResistance
        residualCurrent
      }
    }
  `

  const { plantId, deviceId, lte, gte, step = 1 } = params

  const variables = {
    plantId,
    deviceId,
    timestamp: {
      lte,
      gte,
      step,
    },
  }

  const res = await sendQuery(process.env.REACT_APP_API_URL, query, variables)
  const { logs } = res.data

  return logs.map(log => ({
    ...log,
    energy: parseFloat(log.energy),
    power: parseFloat(log.power),
    current: parseFloat(log.current),
    voltage: parseFloat(log.voltage),
    insulationResistance: parseFloat(log.insulationResistance),
    residualCurrent: parseFloat(log.residualCurrent),
    unixTime: Math.floor(new Date(log.timestamp).getTime() / 1000),
  }))
}
