import React from "react"
import PropTypes from "prop-types"
import { Button } from "semantic-ui-react"
import styles from "./TimeUnitButtons.module.css"

export const UNITS = ["MONTH", "DAY", "HOUR"]

const TEXT = {
  MONTH: "Month",
  DAY: "Day",
  HOUR: "Hour",
}

export function TimeUnitButtons(props) {
  const { active } = props

  function getColor(key) {
    return key == active ? "yellow" : undefined
  }

  return (
    <div className={styles.TimeUnitButtons}>
      {UNITS.map(unit => (
        <Button
          key={unit}
          color={getColor(unit)}
          onClick={() => props.onClick(unit)}
        >
          {TEXT[unit]}
        </Button>
      ))}
    </div>
  )
}

TimeUnitButtons.propTypes = {
  active: PropTypes.oneOf(UNITS).isRequired,
  onClick: PropTypes.func.isRequired,
}

export default TimeUnitButtons
