import React from "react"
import { Route, Switch } from "react-router-dom"
import styles from "./App.module.css"
import withDivSize from "./components/withDivSize"
import HomePage from "./pages/HomePage"
import PowerPlantDetailPage from "./pages/PowerPlantDetailPage"

export function App() {
  return (
    <div className={styles.component}>
      <h5>Time zone: {process.env.REACT_APP_TIME_ZONE}</h5>

      <Switch>
        <Route path="/plants/:plantId" component={PowerPlantDetailPage} />
        <Route component={HomePage} />
      </Switch>

      <div className={styles.footer} />
    </div>
  )
}

export default withDivSize(App)
