import React, { useEffect, useRef } from "react"
import PropTypes from "prop-types"
import { connect } from "react-redux"
import { actions } from "../reducer"

export default function withDivSize(Component) {
  function WithDivSize(props) {
    const ref = useRef()

    useEffect(() => {
      function setDivSize() {
        props.updateDivSize({
          // size of div
          height: ref.current.clientHeight,
          width: ref.current.clientWidth,
        })
      }

      setDivSize()

      window.addEventListener("resize", setDivSize)

      return () => {
        window.removeEventListener("resize", setDivSize)
      }
    }, [])

    return (
      <div ref={ref}>
        <Component {...props} />
      </div>
    )
  }

  WithDivSize.propTypes = {
    updateDivSize: PropTypes.func.isRequired,
  }

  return connect(
    undefined,
    actions.divSize
  )(WithDivSize)
}
