import React, { useState } from "react"
import PropTypes from "prop-types"
import { Checkbox, Dropdown, Button } from "semantic-ui-react"
import ColorBox from "./ColorBox"
import styles from "./DeviceFilter.module.css"

function Row(props) {
  const { onClick, checked, color, text } = props

  return (
    <Dropdown.Item onClick={onClick}>
      <div className={styles.cell}>
        <Checkbox checked={!!checked} />
        <ColorBox color={color} />
        <div>{text}</div>
      </div>
    </Dropdown.Item>
  )
}

export function DeviceFilter(props) {
  const { devices, checked, colors, onToggle } = props

  const [open, setOpen] = useState(false)

  function onClick(id) {
    if (id == "all") {
      onToggle("all", devices.map(({ id }) => id))
    } else {
      onToggle(id)
    }
  }

  const count = Object.values(checked).filter(v => v).length
  const all = count == devices.length

  return (
    <Button.Group basic size="small">
      <Dropdown open={open}>
        <Dropdown.Menu style={{ maxHeight: 200, overflow: "auto" }}>
          <Row
            onClick={() => onClick("all")}
            checked={all}
            color="transparent"
            text={`All ${devices.length} devices`}
          />
          <Dropdown.Divider />
          {devices.map((device, i) => (
            <Row
              key={i}
              onClick={() => onClick(device.id)}
              checked={checked[device.id]}
              color={colors[device.id]}
              text={`${device.model} - ${device.id}`}
            />
          ))}
        </Dropdown.Menu>
      </Dropdown>
      <Button
        labelPosition="right"
        icon="caret down"
        content={`Showing ${count} devices`}
        onClick={() => setOpen(!open)}
      />
    </Button.Group>
  )
}

DeviceFilter.propTypes = {
  devices: PropTypes.arrayOf(
    PropTypes.shape({
      model: PropTypes.string.isRequired,
      id: PropTypes.string.isRequired,
    })
  ).isRequired,
  onToggle: PropTypes.func.isRequired,
  checked: PropTypes.object.isRequired,
  colors: PropTypes.object.isRequired,
}

export default DeviceFilter
