const UPDATE = "DIV_SIZE/UPDATE"

const initialState = {
  width: 0,
  height: 0,
}

export function reducer(state = initialState, action) {
  switch (action.type) {
    case UPDATE:
      return {
        ...state,
        width: action.width,
        height: action.height,
      }
    default:
      return state
  }
}

export const actions = {
  updateDivSize: ({ width, height }) => ({ type: UPDATE, width, height }),
}
