import { useState } from "react"

export default function useToggle(ids) {
  const [checked, setState] = useState(
    ids.reduce((checked, id) => {
      checked[id] = true
      return checked
    }, {})
  )

  function _toggle(id) {
    setState({
      ...checked,
      [id]: !checked[id],
    })
  }

  function _toggleAll(ids = []) {
    const all = Object.values(checked).filter(v => v).length == ids.length

    if (all) {
      setState({})
    } else {
      setState(
        ids.reduce((checked, id) => {
          checked[id] = true
          return checked
        }, {})
      )
    }
  }

  function toggle(id, ids) {
    if (id == "all") {
      _toggleAll(ids)
    } else {
      _toggle(id)
    }
  }

  return [checked, toggle]
}
