function getNumDigits(x) {
  return Math.max(Math.floor(Math.log10(Math.abs(x))), 0) + 1
}

function getDigit(x, n) {
  return Math.floor((x / Math.pow(10, n - 1)) % 10)
}

export function roundInterval(max, min, numSteps) {
  const numDigits = getNumDigits((max - min) / numSteps)
  const lastDigit = getDigit((max - min) / numSteps, numDigits)

  // mapping from last digit to scale
  // 9, 10 -> 10
  // 7, 8 -> 8
  // ...
  // 1, 2 -> 1
  const scale = 2 * Math.ceil(lastDigit / 2)
  return scale * 10 ** (numDigits - 1)
}

export function bound(val, min = -Infinity, max = Infinity) {
  return Math.min(Math.max(val, min), max)
}
