import BarGraph from "./BarGraph"

const NOW = now()
const CURRENT_MONTH = getMonth(NOW)

function getDefaultXRange(unit) {
  switch (unit) {
    case "MONTH":
      return {
        xMin: -(CURRENT_MONTH + 0.5),
        xMax: 11 - (CURRENT_MONTH + 0.5),
      }
    case "DAY":
      return {
        xMin: -30.5,
        xMax: 0.5,
      }
    case "HOUR":
      return {
        xMin: -72.5,
        xMax: 0.5,
      }
    default:
      console.error(`Unknown unit ${unit}`)
  }
}

export function EnergyGraph(props) {
  const {
    plantId,
    devices,
    graphWidth,
    colors,
    params,
    setParams,
    getEnergy,
  } = props

  const [checked, toggle] = useToggle(devices.map(({ id }) => id))

  function onClickTimeUnitButton(unit) {
    setParams({
      ...params,
      unit,
      ...getDefaultXRange(unit),
    })
  }

  function onChangeXRange({ xMin, xMax }) {
    setParams({
      ...params,
      xMin,
      xMax,
    })
  }

  return (
    <>
      <div className={styles.filter}>
        <div className={styles.filterCol}>
          <TimeUnitButtons
            active={params.unit}
            onClick={onClickTimeUnitButton}
          />
        </div>
        <div className={styles.filterCol}>
          <DeviceFilter
            devices={devices}
            checked={checked}
            colors={colors}
            onToggle={toggle}
          />
        </div>
      </div>
      <BarGraph
        plantId={plantId}
        devices={devices}
        width={graphWidth}
        checked={checked}
        colors={colors}
        data={get(getEnergy.response, ["data"], {})}
        yMaxs={get(getEnergy.response, ["yMaxs"], {})}
        xs={get(getEnergy.response, ["xs"], [])}
        loading={getEnergy.fetching}
        {...params}
        onChangeXRange={onChangeXRange}
      />
    </>
  )
}

EnergyGraph.propTypes = {
  plantId: PropTypes.string.isRequired,
  devices: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
    })
  ).isRequired,
  graphWidth: PropTypes.number.isRequired,
  colors: PropTypes.object.isRequired,
  params: PropTypes.shape({
    unit: PropTypes.oneOf(UNITS).isRequired,
    xMin: PropTypes.number.isRequired,
    xMax: PropTypes.number.isRequired,
  }).isRequired,
  setParams: PropTypes.func.isRequired,
  getEnergy: PropTypes.shape({
    fetching: PropTypes.bool.isRequired,
  }).isRequired,
}

function getGte(unit, now, x) {
  switch (unit) {
    case "MONTH":
    case "DAY":
    case "HOUR":
      const _unit = unit.toLowerCase()

      return timeZone(now.toISOString())
        .startOf(_unit)
        .add(Math.round(x) - 1, _unit)
        .toISOString()
    default:
      console.error(`Invalid unit ${unit}`)
  }
}

function getLte(unit, now, x) {
  switch (unit) {
    case "MONTH":
    case "DAY":
    case "HOUR":
      const _unit = unit.toLowerCase()

      return timeZone(now.toISOString())
        .startOf(_unit)
        .add(Math.round(x) + 1, _unit)
        .toISOString()
    default:
      console.error(`Invalid unit ${unit}`)
  }
}

function getUnixTimeFromX(unit, now, x) {
  switch (unit) {
    case "MONTH":
    case "DAY":
    case "HOUR":
      const _unit = unit.toLowerCase()

      return timeZone(now.toISOString())
        .endOf(_unit)
        .add(x, _unit)
        .unix()
    default:
      console.error(`Invalid unit ${unit}`)
  }
}

function getXFromUnixTime({ xMin, xMax, tMin, tMax, t }) {
  const d = (xMax - xMin) / (tMax - tMin)
  const x0 = xMin - d * tMin

  return Math.round(d * t + x0)
}

async function getEnergy(params, props) {
  const { plantId, deviceIds, unit, xMin, xMax } = params

  const now = new Date()

  const gte = getGte(unit, now, xMin)
  const lte = getLte(unit, now, xMax)

  const res = await Promise.all(
    deviceIds.map(async deviceId => {
      const data = await api.getEnergy({
        plantId,
        deviceId,
        unit,
        timestamp: {
          gte,
          lte,
        },
      })

      return {
        deviceId,
        data,
      }
    })
  )

  const tMin = getUnixTimeFromX(unit, now, parseInt(xMin))
  const tMax = getUnixTimeFromX(unit, now, parseInt(xMax))

  const data = res.reduce((map, { deviceId, data }) => {
    map[deviceId] = data.map(d => ({
      ...d,
      x: getXFromUnixTime({
        xMin: parseInt(xMin),
        xMax: parseInt(xMax),
        tMin,
        tMax,
        t: d.unixTime,
      }),
      y: d.diff,
    }))

    return map
  }, {})

  const yMaxs = res.reduce((yMaxs, { deviceId, data }) => {
    yMaxs[deviceId] = Math.max(...data.map(d => d.diff), 0)

    return yMaxs
  }, {})

  return {
    data,
    yMaxs,
    xs: get(data, [deviceIds[0]], []).map(({ x }) => x),
  }
}

